
import React from "react";
import Slider from "react-slick";
import { Box, Typography, Card, CardMedia, CardContent, IconButton } from "@mui/material";
import ArrowBackIosNewIcon from "@mui/icons-material/ArrowBackIosNew";
import ArrowForwardIosIcon from "@mui/icons-material/ArrowForwardIos";

const agents = [
  {
    name: "Agent Pakulla",
    role: "Marketing",
    image: "/images/agent1.svg", // Replace with actual images
  },
  {
    name: "Nina Walker",
    role: "Accountant",
    image: "/images/agent1.svg",
  },
  {
    name: "Tom Wilson",
    role: "Marketing",
    image: "/images/agent1.svg",
  },
  {
    name: "Samuel Smith",
    role: "Accountant",
    image: "/images/agent1.svg",
  },
];

// Custom Arrow Components
const CustomPrevArrow = ({ onClick }) => (
  <IconButton
    onClick={onClick}
    sx={{
      position: "absolute",
      top: "50%",
      left: "-40px",
      transform: "translateY(-50%)",
      zIndex: 1,
      backgroundColor: "white",
      boxShadow: 2,
      "&:hover": { backgroundColor: "grey.200" },
    }}
  >
    <ArrowBackIosNewIcon />
  </IconButton>
);

const CustomNextArrow = ({ onClick }) => (
  <IconButton
    onClick={onClick}
    sx={{
      position: "absolute",
      top: "50%",
      right: "-40px",
      transform: "translateY(-50%)",
      zIndex: 1,
      backgroundColor: "white",
      boxShadow: 2,
      "&:hover": { backgroundColor: "grey.200" },
    }}
  >
    <ArrowForwardIosIcon />
  </IconButton>
);

const OurAgents = () => {
  const settings = {
    dots: true,
    infinite: true,
    speed: 500,
    slidesToShow: 3,
    slidesToScroll: 1,
    nextArrow: <CustomNextArrow />,
    prevArrow: <CustomPrevArrow />,
    responsive: [
      {
        breakpoint: 1024,
        settings: {
          slidesToShow: 2,
          slidesToScroll: 1,
        },
      },
      {
        breakpoint: 600,
        settings: {
          slidesToShow: 1,
          slidesToScroll: 1,
        },
      },
    ],
  };

  return (
    <Box sx={{ textAlign: "center", p: 4 }}>
      <Typography variant="h4" gutterBottom>
        Our Agents
      </Typography>
      <Typography variant="body1" sx={{ mb: 3, color: "text.secondary" }}>
        Lorem ipsum dolor sit amet, consectetur adipiscing elit.
      </Typography>
      <Box sx={{ position: "relative" }}>
        <Slider {...settings}>
          {agents.map((agent, index) => (
            <Card key={index} sx={{ maxWidth: 345, margin: "0 auto" }}>
              <CardMedia
                component="img"
                alt={agent.name}
                height="200"
                image={agent.image}
              />
              <CardContent>
                <Typography variant="h6">{agent.name}</Typography>
                <Typography variant="body2" color="text.secondary">
                  {agent.role}
                </Typography>
              </CardContent>
            </Card>
          ))}
        </Slider>
      </Box>
    </Box>
  );
};

export default OurAgents;
